.card {
  width: 233px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid transparent;
  padding: 10px 15px;
  transition: border 0.3s ease;
  .image {
    width: 100%;
    height: 310px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    span {
      position: absolute;
      top: 5px;
      background-color: white;
      color: teal;
      padding: 3px 5px;
      font-weight: 600;
      font-size: 15px;
    }
  }

  h2 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: -10px;
  }

  h3 {
    font-size: 14px;
    font-weight: 400;
  }

  &:hover {
    border: 1px solid black;
  }
}
