.product {
  padding: 20px 50px;
  display: flex;
  gap: 50px;

  .left {
    flex: 3;
    display: flex;
    gap: 20px;
    .images {
      flex: 1;

      img {
        width: 100%;
        height: 110px;
        object-fit: cover;
        cursor: pointer;
        margin-bottom: 10px;
      }
    }
    .mainImg {
      flex: 7;

      img {
        width: 100%;
        max-height: 900px;
        object-fit: cover;
      }
    }
  }
  .right {
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .price {
      font-size: 30px;
      color: #2879fe;
      font-weight: 500;
    }

    p {
      font-size: 18px;
      font-weight: 300;
      text-align: justify;
    }

    .quantity {
      display: flex;
      align-items: center;
      gap: 10px;

      button {
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border: none;
        font-size: 20px;
        background-color: rgb(223, 223, 223);
        &:hover{
            color: rgb(223, 223, 223);
            background-color: rgb(85, 85, 85);
            transition: ease 0.5s;
        }
      }
    }

    .add {
      width: 250px;
      padding: 10px;
      background-color: #2879fe;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      cursor: pointer;
      border: none;
      font-weight: 500;

      &:hover{
        background-color: rgb(202, 202, 202);
        color: #2879fe;
        transition: ease 0.6s;
      }
    }
  }
}
