.navbar {
  height: 55px;

  .wrapper {
    padding: 10px 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .item {
      display: flex;
      align-items: center;
      font-size: 14px;
      font-weight: 550;

      &:hover{
        transition: 1.5s;
        color: rgb(250, 214, 14);
      }
    }

    .left {
      display: flex;
      align-items: center;
      gap: 12px;
    }
    .center {
      font: 30px;
      letter-spacing: 2px;
    }
    .right {
      display: flex;
      align-items: center;
      gap: 10px;

      .icons {
        display: flex;
        gap: 15px;
        color: #4b4a4a;
        cursor: pointer;

        .cartIcon {
          position: relative;

          span {
            font-size: 12px;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: #2879fe;
            color: white;
            position: absolute;
            right: -10px;
            top: -10px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }
}
