.contact {
  background-color: rgb(236, 236, 236);
  color: balck;
  padding: 15px;
  display: flex;
  justify-content: center;

  .wrapper {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    input {
      padding: 10px;
      border: none;
      border-radius: 5px 0 0 5px;
      width: 250px;
    }

    button {
      padding: 10px;
      color: white;
      background: #333;
      border-radius: 0 5px 5px 0;
      border: none;
      cursor: pointer;
      transition-duration: 0.3s;

      &:hover {
        background: rgb(155, 154, 154);
        color: #191919;
      }
    }

    .icons {
      display: flex;
      gap: 10px;

      .fb:hover {
        color: blue;
        cursor: pointer;
      }
      .ig:hover {
        color: rgb(255, 102, 0);
        cursor: pointer;
      }
    }
  }
}
